"use client";

import { useState } from "react";
import { motion } from "framer-motion";

export default function HomePage() {
  const [image, setImage] = useState<File | null>(null);
  const [analysisResult, setAnalysisResult] = useState<null | {
    skinType: string;
    issues: string[];
    recommendations: string[];
  }>(null);
  const [isAnalyzing, setIsAnalyzing] = useState(false);

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      setImage(e.target.files[0]);
      setAnalysisResult(null);
    }
  };

  const simulateAnalysis = () => {
    if (!image) return;
    setIsAnalyzing(true);
    setTimeout(() => {
      // Mocked analysis result
      setAnalysisResult({
        skinType: "Combination",
        issues: ["Dry patches", "Uneven tone"],
        recommendations: [
          "Use a gentle hydrating cleanser",
          "Apply moisturizer twice daily",
          "Use sunscreen with SPF 30+",
        ],
      });
      setIsAnalyzing(false);
    }, 2000);
  };

  return (
    <main className="min-h-screen bg-cream text-dark px-6 py-12 max-w-7xl mx-auto">
      {/* Hero Section */}
      <section className="text-center max-w-3xl mx-auto mb-20">
        <motion.h1
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          className="text-5xl font-bold mb-4 text-pink-600"
        >
          Skinlytical
        </motion.h1>
        <motion.p
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0.5, duration: 0.8 }}
          className="text-lg text-pink-400"
        >
          AI-powered skin analysis and personalized skincare recommendations
        </motion.p>
        <motion.button
          whileHover={{ scale: 1.05 }}
          whileTap={{ scale: 0.95 }}
          className="mt-8 bg-pink-300 hover:bg-pink-400 text-white font-semibold py-3 px-8 rounded-full shadow-md transition-colors duration-300"
          onClick={() => window.scrollTo({ top: 600, behavior: "smooth" })}
        >
          Get Started
        </motion.button>
      </section>

      {/* Features Section */}
      <section className="mb-20 max-w-4xl mx-auto grid grid-cols-1 md:grid-cols-2 gap-10">
        <motion.div
          initial={{ opacity: 0, x: -30 }}
          whileInView={{ opacity: 1, x: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.8 }}
          className="bg-pink-50 rounded-2xl p-8 shadow-lg"
        >
          <h2 className="text-2xl font-semibold mb-3 text-pink-600">
            Advanced Skin Analysis
          </h2>
          <p className="text-pink-500">
            Using AI technology, we analyze your skin to detect issues and
            provide personalized insights.
          </p>
        </motion.div>
        <motion.div
          initial={{ opacity: 0, x: 30 }}
          whileInView={{ opacity: 1, x: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.8 }}
          className="bg-pink-50 rounded-2xl p-8 shadow-lg"
        >
          <h2 className="text-2xl font-semibold mb-3 text-pink-600">
            Personalized Recommendations
          </h2>
          <p className="text-pink-500">
            Get skincare product recommendations tailored to your unique skin
            needs.
          </p>
        </motion.div>
      </section>

      {/* Skin Analysis Section */}
      <section
        id="skin-analysis"
        className="max-w-4xl mx-auto bg-pink-50 rounded-2xl p-8 shadow-lg"
      >
        <h2 className="text-3xl font-semibold mb-6 text-pink-600 text-center">
          Skin Analysis
        </h2>
        <div className="flex flex-col md:flex-row items-center gap-8">
          <div className="flex-1 w-full max-w-sm">
            <label
              htmlFor="imageUpload"
              className="block mb-2 font-medium text-pink-600 cursor-pointer"
            >
              Upload or take a selfie
            </label>
            <input
              type="file"
              id="imageUpload"
              accept="image/*"
              onChange={handleImageChange}
              className="block w-full text-pink-600 rounded-lg border border-pink-300 p-2 cursor-pointer"
            />
            <button
              onClick={simulateAnalysis}
              disabled={!image || isAnalyzing}
              className="mt-4 w-full bg-pink-300 hover:bg-pink-400 text-white font-semibold py-3 rounded-full shadow-md transition-colors duration-300 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {isAnalyzing ? "Analyzing..." : "Analyze Skin"}
            </button>
          </div>
          <div className="flex-1 w-full max-w-md">
            {analysisResult && (
              <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6 }}
                className="bg-white rounded-2xl p-6 shadow-md"
              >
                <h3 className="text-xl font-semibold mb-4 text-pink-600">
                  Analysis Results
                </h3>
                <p>
                  <strong>Skin Type:</strong> {analysisResult.skinType}
                </p>
                <p className="mt-2">
                  <strong>Detected Issues:</strong>
                </p>
                <ul className="list-disc list-inside text-pink-500">
                  {analysisResult.issues.map((issue, idx) => (
                    <li key={idx}>{issue}</li>
                  ))}
                </ul>
                <p className="mt-2">
                  <strong>Recommendations:</strong>
                </p>
                <ul className="list-disc list-inside text-pink-500">
                  {analysisResult.recommendations.map((rec, idx) => (
                    <li key={idx}>{rec}</li>
                  ))}
                </ul>
              </motion.div>
            )}
          </div>
        </div>
      </section>
    </main>
  );
}
